import React from 'react';
import DatePicker from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'
import Autocomplete from 'react-google-autocomplete';

import { Form } from 'semantic-ui-react'

class IndexSearchBox extends React.Component {
    options = [
        {text: "1", value: "1"},
        {text: "2", value: "2"},
        {text: "3", value: "3"},
        {text: "4", value: "4"},
        {text: "5+", value: "5+"}
    ]
    constructor(props) {
        super(props);
        this.state = {
            location: (this.props.location)?this.props.location.name:"Ottawa, ON",
            date: (this.props.date) ? new Date(this.props.date) : new Date(),
            guests: this.props.guests || "2"
        };
    }

    setDate = (event) => {
        this.setState({
            date: new Date(event)
        });
    };

    submit = (event) => {
        event.preventDefault();
        var queryString = "";
        queryString = `?location=${this.state.location}&date=${this.state.date}&guests=${this.state.guests}`
        window.location.href = this.props.method + queryString;
    };

    onChange = (abd,event) => {
        console.log(abd);
        this.setState({ [event.name]: event.value });
    }

    onLocationChange = (event) => {
        this.setState({ "location": event.target.value });
    }

    render() {
        return (
            <Form className="ui form container" onSubmit={this.submit} autocomplete='off' style={this.props.style}>
                <h1 className="ui dividing header">Find your next tasting.</h1>
                {
                    !this.props.location &&

                    <div className="field">
                        <label>Where</label>
                        <Autocomplete
                            type="text"
                            ID="location"
                            name="location"
                            onPlaceSelected={(place) => {
                                this.setState({
                                    location: place.formatted_address
                                })
                            }}
                            value={this.state.location}
                            onChange={this.onLocationChange}
                            types={['(regions)']}
                        />
                    </div>
                }
                {
                    this.props.location &&
                    <div className="field">
                        <label>Where</label>
                        <Form.Input
                            type="text"
                            ID="location"
                            name="location"
                            disabled
                            value={this.props.location.name}
                        />
                    </div>
                }
                <div className="field">
                    <label>When</label>
                    <input hidden={true} value={this.state.date} name="date" required />
                    <DatePicker
                        selected={this.state.date}
                        onChange={this.setDate}
                    />
                </div>
                <div className="field">
                    <div className="field">
                        <label>Guests</label>
                        <Form.Dropdown name="guests" placeholder="Guests" fluid selection options={this.options} onChange={this.onChange} value={this.state.guests}  />
                    </div>
                </div>
                <div ref={el => (this.instance = el)}></div>
                <button className="ui purple button right floated">Search</button>
            </Form>
        );
    }
}

export default IndexSearchBox;