import React from 'react'
import Wine from './Wine'

export default class WineList extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="winery-grid">
                {
                    this.props.wines.map(x => {
                        return <Wine wine_id={x}></Wine>
                    })
                }
            </div>
        )
    }
}