import React from 'react'
import { Link } from 'react-router-dom'

class WineryListItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = { isLoading: true }
    }

    loadingTask = () => {
        this.setState({ isLoading: false });
    }

    selectWinery = () => {
        this.props.history.push("/Winery/" + this.props.item._id, {date: this.props.date, guests: this.props.guests})
    }

    moveTo = (item) => {
        const args = {
            lat: item.location.coordinates[1],
            lng: item.location.coordinates[0],
            _id: item._id
        };
        this.props.onHover(args);

    }

    onListItemClicked = this.props.onListItemClicked;

    render() {
        return (
            <div className="ui item winery-list-item" style={!this.props.grid ? { } : { marginTop: "10px",gridTemplateColumns: "auto", height:"400px" }} onClick={this.selectWinery}>
                <div className={this.props.grid ? "ui image" : "list-image"} style={{width:"100%", height:"100%", background:`url(${process.env.REACT_APP_API_URL + "/winery/photo/" + this.props.item.photos[0]})`, backgroundSize:"cover"}}>
                </div>
                {
                    !this.props.grid &&
                    <div className="content">
                        <p className="header">{this.props.item.name}</p>
                        <div className="meta">
                            <span>{this.props.item.address}</span>
                        </div>
                        <div className="description">
                            <p>{this.props.item.phone}</p>
                        </div>
                        <div className="extra">
                            Additional Details
                        </div>
                    </div>
                }
            </div>
        );
    }
}

export default WineryListItem;