import React from 'react'
import HorizontalScroll from 'react-scroll-horizontal'

export default class Wine extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div class="winery-photo-viewer">
                <HorizontalScroll>
                    {
                        this.props.pictures.map(x => {
                            return (<img src={process.env.REACT_APP_API_URL + "/winery/photo/" + x} />)
                        })
                    }
                    {
                        this.props.pictures.map(x => {
                            return (<img src={process.env.REACT_APP_API_URL + "/winery/photo/" + x} />)
                        })
                    }
                    {
                        this.props.pictures.map(x => {
                            return (<img src={process.env.REACT_APP_API_URL + "/winery/photo/" + x} />)
                        })
                    }
                </HorizontalScroll>
            </div>
        )
    }
}