import React from 'react';
import './Index.css';

export default class Booking extends React.Component {
    constructor(props) {
        super(props);
        var url = this.props.location.search.substring(1);
        this.state = JSON.parse('{"' + decodeURI(url).replace(/"/g, '\\"').replace(/&/g, '","').replace(/=/g, '":"') + '"}');

    }

    render() {
        return (
            <div className="">
                {
                    Object.keys(this.state).map((a, b) => {
                        return (
                            <div>
                                {a} : {this.state[a]}
                            </div>
                        )
                    })
                }
            </div>
        );
    }
};
